
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  background:#8758ff;
  display: flex;
  justify-content: center;
  align-self: center;
}
.App {

  text-align: center;
}

h1{
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 1.75rem;
}
.todolist{
  background: #1A1A40;
  margin-top:5rem;
  padding: 2rem;
  border-radius: 5px;

}
.todoform{
  width: 100%;
  margin-bottom: 1rem;
}

.todoentry{
  outline: none;
  background: none;
  border: 1px solid #8758ff;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
}
todoentry::placeholder{
  color: #ffffff4d;
}
.add-btn{
  background-color: #8758ff;
  color: #fff;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
}

.todo{
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #8758ff;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin: 1rem;
}
.fa-trash{
  margin-left: 0.75rem;
}
.completed{
  color: #c5aeff;
  text-decoration: line-through;
  cursor: pointer;
}
.incomplete{
  color:#fff;
  cursor: pointer;
}
